import {request} from "@/network/request";

/**
 * 
 * @param userId  id
 */
export function queryMemosByUserIdNetwork(userId){
  return request({
    url: '/memo/userMemo',
    params: {
      userId
    },
    method: 'post'
  })
}


/**
 * 
 * @param userId  id
 * @param memoId  id
 * @returns {AxiosPromise}
 */
export function removeUserMemoNetwork(userId, memoId){
  return request({
    url: '/memo/removeMemo',
    params: {
      userId,
      memoId
    },
    method: 'post'
  })
}

/**
 * 
 * @param userId
 * @param memo
 * @param staleDate
 * @param createDate
 * @returns {AxiosPromise}
 */
export function addMemoNetwork(userId, memo, staleDate, createDate){
  return request({
    url: '/memo/add',
    params: {
      userId,
      memo,
      staleDate,
      createDate
    },
    method: 'post'
  })
}

/**
 * 
 * @param userId
 * @param memoId
 * @returns {AxiosPromise}
 */
export function confirmMemoNetwork(userId, memoId){
  return request({
    url: '/memo/confirmMemo',
    params:{
      userId,
      memoId
    },
    memoId: 'post'
  })
}