//coolie 

export function cookie(name){

  var cookieArray=document.cookie.split("; "); //cookie

  for (var i=0;i<cookieArray.length;i++){

    var arr=cookieArray[i].split("=");       //

    if(arr[0]==name)return unescape(arr[1]); //cookie

  }

  return "";

}



