<template>
  <div class="navbar" :style="navbarStyle">
    <div class="line"></div>

<!--  -->
    <memo-drawer
        v-loading="loading"
      :is-show-memo="drawer"
      :memo="memo"
      @removememo="removeMemo"
      @addmemo="addMemo"
      @confirmfinish="confirmFinish"
    />

    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      active-text-color="#ffd04b"
      background-color="transparent"
      @select="handleSelect">


      <el-menu-item style="margin-left: 57px"
                    @click="pageback" index="2">
        <el-tooltip effect="dark"
                    content=""
                    placement="bottom">
          <i class="el-icon-back" style="font-size: 24px"></i>
        </el-tooltip>
      </el-menu-item>`

      <el-menu-item  v-if="isLogin" index="3">
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            <el-tooltip effect="dark"
                        content=""
                        placement="bottom">
              <i class="el-icon-menu" style="font-size: 24px"></i>
            </el-tooltip>
          </span>

          <el-dropdown-menu slot="dropdown">
            <div style="width: 220px;margin: 10px;text-align: center">
              <el-row>

<!--                -->
                <el-col :span="8">
                  <el-tooltip effect="dark"
                              content=""
                              placement="bottom">

                    <i class="el-icon-edit-outline more-func-icon"
                       :key="'openDrawer'"
                       @click.stop="openDrawer">
                    </i>
                  </el-tooltip>
                  <div></div>
                </el-col>

<!--            -->
                <el-col :span="8">
                  <el-tooltip effect="dark"
                              content=""
                              placement="bottom">
                    <i class="el-icon-s-comment more-func-icon"
                       @click="openInform"
                       :key="'openInform'"
                      ></i>
                  </el-tooltip>
                  <div></div>
                </el-col>


                <el-col :span="8">
                  <el-link :href="$store.getters.getApiDocSrc" target="_blank">
                  <el-tooltip effect="dark"
                              content=""
                              placement="bottom">
                    <i class="el-icon-document more-func-icon"
                       :key="'openInform'"
                    ></i>
                  </el-tooltip>
                  </el-link>
                  <div></div>
                </el-col>
              </el-row>

            </div>

          </el-dropdown-menu>
        </el-dropdown>
      </el-menu-item>


  <!---->
    <el-submenu v-if="isLogin"
                id="user-manage"
                index="1">
      <template slot="title">
        <div class="navbar-username">
            <el-avatar :src="headImg" ></el-avatar>
             {{user.userName}}
        </div>
      </template>

      <el-menu-item @click="toUserInfoPage" index="1-1"></el-menu-item>
      <el-menu-item @click="toFaceRegisterPage" index="1-2"></el-menu-item>
      <el-menu-item index="1-3" @click="open"></el-menu-item>
      <el-menu-item>
        <el-dropdown>
            <span style="color: red" class="el-dropdown-link">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
          <el-dropdown-menu slot="dropdown" >
            <el-dropdown-item icon="el-icon-plus"></el-dropdown-item>
            <el-dropdown-item icon="el-icon-delete">
              <span @click="deleteFaceInfo"></span>
            </el-dropdown-item>
            <el-dropdown-item icon="el-icon-circle-check">
            <span @click="alterPwd"> </span>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-menu-item>
    </el-submenu>

  <!--    -->
    <el-menu-item v-if="!isLogin" id="not-login" @click="toLoginPage">
      
    </el-menu-item>

  </el-menu>

  </div>
</template>

<script>
import MemoDrawer from '@/components/memodrawer/MemoDrawer'

import {queryMemosByUserIdNetwork,removeUserMemoNetwork
  ,addMemoNetwork, confirmMemoNetwork} from "@/network/memo";
import {faceDropNetWork} from "@/network/face";

export default {

  components: {
    MemoDrawer
  },
  computed: {
    navbarStyle() {
      return this.$store.getters.getNavbarBgi
    },
    isLogin() {
      return this.$store.getters.getLoginUser !== null
    }
  },
  data() {
    return {
      activeIndex: '1',
      drawer: false,
      memo: [],
      headImg: require('@/assets/img/head_default.png'),
      loading: false
    };
  },
  props:{
    user:{
      type: Object,
      default(){
        return {userName: '', trueName: ''}
      }
    },
    navbarBgc: {
      type: String,
      default: 'transparent'
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    //
    toLoginPage(){
      this.$router.push('/login')
    },
    toUserInfoPage(){
      this.$router.push('/index/myinfo')
    },
    toFaceRegisterPage() {
      this.$router.push('/index/faceregisterpage')
    },
    alterPwd(){
      console.log('---')
      this.$router.push('/index/passwordalter')
    },
    openInform(){
      this.$router.push('/index/informview')
    },
    pageback(){
      this.$router.back()
    },

    logout(){
      this.$emit('logout')
    },

    //
    open() {
      this.$confirm('', '', {
        distinguishCancelAndClose: true,
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(() => {

        this.logout()
        this.$message({
          type: 'info',
          message: ''
        })
      }).catch(e=>{})
    },

    load(){
      this.drawer = true
      let userId = this.$store.getters.getLoginUser.id
      this.loading = true
      queryMemosByUserIdNetwork(userId).then(data=>{
        if(data.code === 200){
          this.memo.splice(0,this.memo.length)
          this.memo.push(...data.result)
        }else{
          this.$message.error('', data.msg)
        }
      }).finally(()=>{
        this.loading = false

      })
    },

    //
    openDrawer(){
      this.load()
    },
    //
    removeMemo(memoId){
      let userId = this.$store.getters.getLoginUser.id
      removeUserMemoNetwork(userId, memoId).then(data=>{
        console.log(data)
        if(data.code === 200){
          this.$message.success('')
          this.load()
        }else {
          this.$message.error(''+data.msg)
        }
      })
    },
    /**
     * 
     */
    addMemo(info){
      let userId = this.$store.getters.getLoginUser.id
      let memo = info.memo
      let staleDate = info.staleDate
      let createDate = this.$formatDate(new Date().getTime())
      console.log(staleDate, memo, createDate)
      addMemoNetwork(userId,memo,staleDate,createDate).then(data=>{
        console.log(data)
        if(data.code === 200){
          this.$message.success('')
          this.load()
        }else{
          this.$message.error(''+data.msg)
        }
      })
    },
    /**
     * 
     * @param memoId
     */
    confirmFinish(memoId){
      let userId = this.$store.getters.getLoginUser.id
      confirmMemoNetwork(userId, memoId).then(data=>{
        console.log(data)
        if(data.code === 200){
          this.$message.success('')
          this.load()
        }else {
          this.$message.error(''+data.msg)
        }
      })
    },

    /**
     * 
     */
    deleteFaceInfo() {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let userId = this.$store.getters.getLoginUser.id
        faceDropNetWork(userId).then(data=>{
          if(data.code === 200){
            this.$message.success('')
          }else {
            this.$message.error(''+data.msg)
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    }

  }
}
</script>

<style scoped>
.navbar-username{
  width: 100%;
  overflow: hidden;
}
.navbar-username:hover{
  overflow: visible;
}
#user-manage{
  position: absolute;
  right: 0;
  top: 0;
  margin-right: 10px;
}

#not-login{
  position: absolute;
  top: 0;
  right: 0;
}

.more-func-icon{
  font-size: 45px;
}
.more-func-icon:hover{
  cursor: pointer;
  color: #ffcf49;
}
</style>