import {request} from "./request";

/**
 * 
 * @param userName 
 * @param userPwd 
 * @returns {
    "msg": "",
    "code": 200,
    "result": {
        "userIdStr": "##gM5MjM##QOzkTN1EDM5MjMzYTM",
        "username": "lisi",
        "trueName": ""
    }}
 */
export function loginNetwork(userName, userPwd){
  return request({
    url: '/user/login',
    params: {
      userName,
      userPwd
    }
  })
}

/**
 * id 
 * @param userIdStr
 * @returns {AxiosPromise}
 */
export function findUserByUserIdStr(userIdStr){
  return request({
    url: '/user/findByIdStr',
    params: {
      userIdStr
    },
    method: 'post'
  })
}

/**
 * 
 * @param user
 * @returns {AxiosPromise}
 */
export function createAccount(user) {
  return request({
    url: '/user/add',
    params: {
      userName: user.userName,
      userPwd: user.userPwd,
      trueName: user.trueName,
      email: user.email,
      phone: user.phone
    },
    method: 'post'
  })
}

/**
 * 
 * @param from
 * @returns {AxiosPromise}
 */
export function alterPwdNetwork(from){
  return request({
    url: '/user/alterPassword',
    params: {
      id: from.id,
      oldPwd: from.oldPwd,
      newPwd: from.newPwd,
      confirmPwd: from.confirmPwd
    },
    method: 'post'
  })
}

/**
 * id
 * @param userId
 * @returns {AxiosPromise}
 */
export function queryUserByIdNetwork(userId){
  return request({
    url: '/user/queryUserById',
    params: {
      userId
    },
    method: 'post'
  })
}